		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.swiper{padding: 148px 0 0;height: 768px;position: relative;box-sizing: border-box;
				&-container{height: 510px;}
				&-wrapper{}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 325px;left: 15px;width: 44px;height: 72px;background: url("/assets/images/club_photo_shoot/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 325px;right: 15px;width: 44px;height: 72px;background: url("/assets/images/club_photo_shoot/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
				&-slide{padding-top: 0;z-index: 1;}
			}
			.clubhouse-photo-shoot{
				&__area{}
				&__box{}
				&__bg{}
				&__items{width: 826px;height: 471px;margin: 0 auto;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
					&.length1{
						.clubhouse-photo-shoot__item{
							&:nth-of-type(1){margin: 0 40px;}
							&:nth-of-type(2){display: none;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length2{
						.clubhouse-photo-shoot__item{
							&:nth-of-type(1){margin: 0 40px;}
							&:nth-of-type(2){margin: 0 40px;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length3{
						.clubhouse-photo-shoot__item{
							&:nth-of-type(1){margin-right: 40px;}
							&:nth-of-type(2){margin-right: 40px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length4{
						.clubhouse-photo-shoot__item{
							&:nth-of-type(1){margin: 0 40px;}
							&:nth-of-type(2){margin: 0 40px;}
							&:nth-of-type(3){margin: 44px 40px 0 190px;}
							&:nth-of-type(4){margin: 44px 40px 0;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length5{
						.clubhouse-photo-shoot__item{
							&:nth-of-type(1){margin-right: 40px;}
							&:nth-of-type(2){margin-right: 40px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){margin: 44px 87px 0 0;}
							&:nth-of-type(5){margin: 44px 0 0;}
						}
					}
				}
				&__item{position: relative;width: 174px;height: 204px;display: block;transform: scale(1);transition: transform 0.3s;
					@for $i from 1 through 4{
						&.bg-color0#{$i}{background: url("/assets/images/club_photo_shoot/bg_item0#{$i}.png") no-repeat 0 0;
							&.complete{
								.complete-box{
									&:before{content: "";position: absolute;top: -2px;left: 34px;width: 100px;height: 25px;background: url("/assets/images/club_photo_shoot/bg_item0#{$i}_complete.png") no-repeat 0 0;z-index: -1;}
								}
							}
						}
					}
					&.active{
						&:not(.disabled):not(.vacation){transform: scale(1.05);}
					}
					&.default{
					}
					&.disabled{
						.tit-week{opacity: 0.4;}
					}
					&.complete{
						.complete-box{
							display: block;position: absolute;bottom: -40px;width: 100%;height: 38px;text-align: center;
						}
					}
					&.this-week{
						.btn:before{content: "";position: absolute;top: -30px;right: -55px;width: 276px;height: 240px;animation: this-week-icon 1s steps(2) infinite forwards;background: url("/assets/images/sprite/club_house_photo_shoot_this_week.png") no-repeat 0 0;z-index: 1; }
						.tit-week{color: #fff509;}
					}
					&.vacation{
						background: url("/assets/images/club_photo_shoot/bg_item_vacation.png") no-repeat 0 0 !important;
						.tit-week{opacity: 0.6;}
					}
					.complete-box{display: none;}
					.tit-week{
						position: absolute;bottom: 8px;left: 0;width: 100%;height: 26px;display: flex;font-family: $nsroundecp;font-size: 27px;color: #fff;letter-spacing: 1px;align-content: center;justify-content: center;
						img{vertical-align: top;}
						.number-box{margin: 3px 0 0 10px;}
					}
					.btn{display: block;width: 100%;height: 100%;}
					.img{position: absolute;bottom: 39px;left: -5px;overflow: hidden;width: 185px;height: 152px;}
					.icon{
						&-star{display: inline-block;width: 40px;height: 38px;background: url("/assets/images/club_photo_shoot/icon_star.png") no-repeat 0 0;}
					}
				}
			}
		}
	//}
//}


